<template>
  <div class="hc-location">
    <div class="hc-location-icon">
      <svg class="icon active" aria-hidden="true">
        <use xlink:href="#icon-ditu"></use>
      </svg>
    </div>
    <div class="hc-location-btn">
      <mt-button @click="toHome">点击获取位置</mt-button>
      <div class="hc-f14">点击获取位置，自动匹配最近门店</div>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    toHome(){
      this.$store.commit('SCROLL_TOP', 100);
      this.$router.push({ name: 'mdc' ,meta: 'fds'});
    }
  }
}
</script>

<style lang="scss">
.hc-location {
  padding-top: 100px;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  font-size: 120px;
  text-align: center;
  .hc-location-btn {
    margin-top: -50px;
    button {
      background-color: #fff;
      width: 150px;
      border: solid 2px #f3ab03;
      height: 48px;
      border-radius: 24px;
      font-size: 17px;
      color: #f3ab03;
    }
    div {
      padding-top: 24px;
      color: #bfbfbf;
    }
  }
}
</style>



